.service {
  display: flex;
  overflow: hidden;
  justify-content: space-evenly;
}
.serviceitem {
  padding: 16px;
}
.serviceitem img {
  max-width: 48px;
  display: grid;
  margin: 0 auto;
}

.servicename {
