.iheader {
  display: flex;
  justify-content: space-evenly;
  height: 51px;
  border-bottom: 0.1rem silver solid;
}
.centertitle {
  line-height: 51px;
  width: 40%;
  text-align: center;
  justify-content: space-between;
  font-size: 1.2rem;
  color: rgba(50, 133, 255, 1);
}
.banner {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 14px 14px 0 0;
}
.divider {
  height: 3px;
  background-color: #eee8e8 !important;
}
.copyright {
  width: 100%;
  text-align: center;
  padding-bottom: 14px;
  color: rgba(50, 133, 255, 1);
  border-radius: 0 0 14px 14px;
}
.indexwraper {
  background-color: #eee8e8;
  padding: 8px;
}
.indexwraper * {
  background-color: white;
}
